html {
  font-size: 22px;
  line-height: 1.25;
}

@media all and (max-width: 2048px) {
  html {
    font-size: calc(14px + 10 * ((100vw - 769px) / 2048));
  }
}

@media all and (max-width: 767px) {
  html {
    font-size: calc(16px + 2 * ((100vw - 360px) / 768));
  }
}

button, p, a, li {
  font-size: 1rem;
}

picture,
img {
  max-width: 100%;
  height: auto;
  display: block;
}

img.lazy {
  background-image: url(data:image/gif;base64,R0lGODlhCgAIAIABAN3d3f///yH5BAEAAAEALAAAAAAKAAgAAAINjAOnyJv2oJOrVXrzKQA7);
}

img.lazy.loaded {
  background-image: none;
}

.slick-slide img.lazy {
  background-image: none;
}

.site-button {
  font-size: 1rem;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html, body {
  position: relative;
  height: 100%;
}

.wrapper {
  min-height: 100vh;
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  justify-content: flex-start;
}

article {
  overflow: hidden;
}

footer {
  margin: auto 0 0;
}

.body-overflow {
  overflow: hidden;
  overscroll-behavior-y: none;
  -webkit-overflow-scrolling: auto;
}

.container {
  max-width: 1160px;
  width: 100%;
  position: relative;
  margin: 0 auto;
  padding: 0 30px;
}

@media all and (max-width: 1023px) {
  .container {
    padding: 0 25px;
  }
}

@media all and (max-width: 767px) {
  .container {
    padding: 0 20px;
  }
}

.sr-only,
select {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  width: 1px;
  height: 1px;
  overflow: hidden;
  padding: 0;
  border: 0;
  white-space: nowrap;
}

.contain-picture {
  object-fit: contain !important;
}

a {
  text-decoration: none;
  color: inherit;
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

button {
  cursor: pointer;
}

b,
strong {
  font-size: inherit;
  font-weight: inherit;
}

b,
strong {
  font-weight: bolder;
}

ul, ol {
  list-style: none;
}

i {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

.fw {
  display: flex;
}

.dc {
  display: flex;
}

@supports (display: grid) {
  .dc {
    display: grid;
    grid-template-columns: minmax(0, 1fr);
  }
}

.dc.vc {
  align-items: center;
}

.dc.hs {
  justify-content: flex-start;
}

.dc.hc {
  justify-content: center;
}

@supports (display: grid) {
  .dc.hc {
    justify-items: center;
  }
}

.dc.he {
  justify-content: flex-end;
}

.dc.wrap {
  display: flex;
  flex-wrap: wrap;
}

.icon, .icon-dots, .icon-facebook, .icon-instagram, .icon-play-button, .icon-youtube {
  display: inline-block;
  width: 1em;
  height: 1em;
  fill: currentColor;
}

.arrows .arrow {
  border-color: #fff;
}

.arrows .arrow::after {
  border-color: inherit;
}

.arrow {
  width: 32px;
  height: 32px;
  display: block;
  border-radius: 50%;
  background-color: transparent;
  margin: 0 25px 0 0;
  border: 1px solid #fff;
  position: relative;
}

@media all and (max-width: 767px) {
  .arrow {
    width: 30px;
    height: 30px;
    margin: 0 8px 0 0;
  }
}

.arrow:last-child {
  margin: 0;
}

.arrow:focus {
  opacity: .5;
}

.arrow::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 8px;
  height: 8px;
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
}

@media all and (max-width: 767px) {
  .arrow::after {
    width: 5px;
    height: 5px;
  }
}

.arrow.is-prev::after {
  transform: translate(-30%, -50%) rotate(45deg);
}

.arrow.is-next::after {
  transform: translate(-70%, -50%) rotate(-135deg);
}

.dots {
  position: absolute;
  bottom: 0;
  right: 0;
}

.dots::after {
  content: "";
  position: absolute;
  z-index: 1;
  background-color: #fff;
}

.dots__img {
  position: absolute;
  z-index: 3;
  top: 50%;
  left: 50%;
  background-image: url("../img/dots.svg");
  background-size: 23px;
  background-repeat: repeat;
}

.tabs-nav {
  padding: 0 0 0 100px;
  position: relative;
}

.tabs-nav::before {
  content: "";
  position: absolute;
  left: 0;
  top: 10px;
  width: 50px;
  height: 2px;
  display: block;
  transform: translateX(50%);
}

.tabs-nav__list {
  flex-wrap: wrap;
  justify-content: flex-start;
  margin: 0;
}

.tabs-nav__item {
  margin: 0 15px 15px 0;
  font-family: 'OptimaCyr', sans-serif;
}

.tabs-nav__button {
  font-family: inherit;
  border: 0;
  background-color: transparent;
  font-size: 0.77778rem;
  text-transform: uppercase;
  cursor: pointer;
}

.tabs-nav__button.is-active {
  font-family: 'OptimaCyrBold', sans-serif;
}

.play-button {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  width: 70px;
  height: 70px;
  border: 0;
  background-color: transparent;
  opacity: 1;
}

.play-button svg {
  width: inherit;
  height: inherit;
  fill: #fff;
  transition: fill .25s;
}

.form {
  flex-wrap: wrap;
}

.form__row {
  width: 100%;
  margin: 0 0 40px;
}

.form__row:last-child {
  margin: 0;
}

.form__label {
  display: inline-block;
  width: 100%;
  height: 100%;
  position: relative;
}

.form__label.is-error .form__error {
  opacity: 1;
}

.form__input, .form__textarea {
  display: block;
  width: 100%;
  height: 100%;
  padding: 10px 0;
  border: 0;
  background-color: transparent;
  border-bottom-width: 1px;
  border-style: solid;
  font-size: 0.88889rem;
  font-family: 'OptimaCyr', sans-serif;
}

.form__input::placeholder, .form__textarea::placeholder {
  font-size: inherit;
  color: inherit;
}

.form__textarea {
  resize: none;
}

.form__error {
  position: absolute;
  width: auto;
  top: 100%;
  left: 0;
  color: tomato;
  pointer-events: none;
  opacity: 0;
  transition: opacity	.25s;
  color: tomato;
}

.form__error.is-white {
  color: #fff;
}

.form__error.is-opened {
  opacity: 1;
}

.form.is-cols {
  margin: 0 -10px;
}

@media all and (max-width: 1023px) {
  .form.is-cols {
    margin: 0;
  }
}

.form.is-cols .form__row {
  width: 50%;
  padding: 0 10px;
}

@media all and (max-width: 1023px) {
  .form.is-cols .form__row {
    width: 100%;
    padding: 0;
  }
}

.form.is-cols .form__row:last-child {
  padding-top: 50px;
  width: 100%;
}

@media all and (max-width: 1023px) {
  .form.is-cols .form__row:last-child {
    padding: 0;
  }
}

.close-button {
  display: inline-block;
  width: 40px;
  height: 40px;
  position: relative;
  border: 0;
}

.close-button::before, .close-button::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 1px;
  display: block;
  background-color: #fff;
}

.close-button::before {
  transform: translate(-50%, -50%) rotate(45deg);
}

img {
  width: 500px;
  height: 500px;
}

body {
  background-color: tomato;
}
