$mobileFotnSize: calc(16px + 2 * ((100vw - 360px) / 768));
$desktopFontSize: calc(14px + 10 * ((100vw - 769px) / 2048));

@mixin or {
	font-family: 'OptimaCyr', sans-serif; }


@mixin ob {
	font-family: 'OptimaCyrBold', sans-serif; }


@mixin ori {
	font-family: 'OptimaCyrItalic', sans-serif; }

html {
	font-size: 22px;
	line-height: 1.25;
	@include r(2048) {
		font-size: $desktopFontSize; }
	@include r(767) {
		font-size: $mobileFotnSize; } }

// h1,h2,h3,h4,h5,h6,p
// 	margin: 0
// 	padding: 0

button, p, a, li {
	font-size: rem(18); }



picture,
img {
	max-width: 100%;
	height: auto;
	display: block; }


img {
	&.lazy {
		background-image: url(data:image/gif;base64,R0lGODlhCgAIAIABAN3d3f///yH5BAEAAAEALAAAAAAKAAgAAAINjAOnyJv2oJOrVXrzKQA7);
		&.loaded {
			background-image: none; } } }


.slick-slide {
	img.lazy {
		background-image: none; } }
